import { connect } from 'react-redux'
import FileUploadProgress from 'Shared/components/FileUploadProgress/FileUploadProgress'
import { uploadedFilesProgress } from 'Shared/selectors/files'

const mapStateToProps = (state, ownProps) => ({
  filesProgress: uploadedFilesProgress(state, ownProps.fileType)
})

export default connect(mapStateToProps)(FileUploadProgress)



