import startCase from 'lodash/startCase'
import { validateUrlWithProtocol } from 'Shared/helpers/string'
import { lengthLimit } from 'Shared/constants/Editor'
import { getEditorContentLength } from 'Shared/helpers/editor'

export default values => {
  const errors = {
    socialMediaLinks: {}
  }

  const requiredFields = [
    {
      name: 'name',
      label: 'name'
    },
    {
      name: 'industryId',
      label: 'industry'
    },
    {
      name: 'size',
      label: 'size'
    },
    {
      name: 'location',
      label: 'location'
    },
    {
      name: 'description',
      label: 'description'
    },
    {
      name: 'logoUrl',
      label: 'companyLogo'
    },
    {
      name: 'uploadFile',
      label: 'uploadFile'
    }
  ]

  const socialMediaLinks = [
    'twitter',
    'linkedin'
  ]

  const [min, max] = lengthLimit.companyDetailsDescription

  if (values['website'] && validateUrlWithProtocol(values['website'])) {
    errors['website'] = {
      key: 'errors.invalidLink',
      options: {}
    }
  }

  socialMediaLinks.forEach(field => {
    if (values['socialMediaLinks'][field] && validateUrlWithProtocol(values['socialMediaLinks'][field])) {
      errors['socialMediaLinks'][field] = {
        key: 'errors.invalidLink',
        options: {}
      }
    }
  })

  if (values['website'] && validateUrlWithProtocol(values['website'])) {
    errors['website'] = {
      key: 'errors.invalidLink',
      options: {}
    }
  }

  if (values['description'] && getEditorContentLength(values['description']) < min) {
    errors['description'] = {
      key: 'errors.textFieldTooShort',
      options: {
        field: startCase('description'),
        chars: startCase(min.toString())
      }
    }
  }

  if (values['description'] && getEditorContentLength(values['description']) > max) {
    errors['description'] = {
      key: 'errors.textFieldTooLong',
      options: {
        field: startCase('description'),
        chars: startCase(max.toString())
      }
    }
  }

  if (values.location && Object.values(values.location).some(key => !key)) {
    errors['location'] = {
      key: 'errors.requiredField',
      options: {
        field: startCase('location')
      }
    }
  }

  requiredFields.forEach(field => {
    if (!values[field.name]) {
      errors[field.name] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field.label)
        }
      }
    }
  })
  return errors
}



