import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Label from 'Shared/components/FormElements/Label'
import DeleteModal from 'Shared/components/DeleteModal'
import { sortByDateAsc } from 'Shared/helpers/date'
import Button from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import S3Service from 'Shared/services/S3Service'
import FilesUploadProgress from 'Shared/connectors/Components/FilesUploadProgressConnector'
import './UploadedImagesList.scss'

const s3Service = new S3Service()

class UploadedImagesList extends Component {
  static propTypes = {
    files: PropTypes.array.isRequired,
    deleteFile: PropTypes.func.isRequired
  }

  state = {
    fileRemoveId: '',
    fileRemoveName: '',
    showDeleteFileModal: false
  }

  _openDeleteModal = (fileId, name) => () => this.setState({ fileRemoveId: fileId, fileRemoveName: name, showDeleteFileModal: true })
  _hideDeleteModal = () => this.setState({ fileRemoveId: '', showDeleteFileModal: false })

  _deleteFile = () => {
    const { fileRemoveId, fileRemoveName } = this.state
    this.props.deleteFile(fileRemoveId, fileRemoveName)
    this._hideDeleteModal()
  }

  render () {
    const { files, t } = this.props
    const { fileRemoveName } = this.state
    return (
      <div className='uploaded-images-list'>
        <Label>{t('common.uploadedImages')}</Label>
        {files
          .slice()
          .sort(sortByDateAsc)
          .map(file => <div key={file.id} className='uploaded-images-list__image'>
            <div className='uploaded-images-list__thumbnail' style={{backgroundImage: `url(${s3Service.generateFileLink(file.path)})`}} />
            <div className='uploaded-images-list__filename'>{file.name}</div>
            <div className='uploaded-images-list__button'>
              <Button justIcon type='button' onClick={this._openDeleteModal(file.id, file.name)}><i className='ion-md-trash' /></Button>
            </div>
          </div>)}
        <FilesUploadProgress />
        <DeleteModal
          title={this.props.t('remove.file.title', { name: fileRemoveName })}
          content={this.props.t('remove.file.description', { name: fileRemoveName })}
          handleDelete={this._deleteFile}
          isModalVisible={this.state.showDeleteFileModal}
          hideDeleteModal={this._hideDeleteModal}
        />
      </div>
    )
  }
}

export default withTranslations(UploadedImagesList)



