import React from 'react'
import * as R from 'ramda'
import './FileUploadProgress.scss'

const progressStyles = (min, max) => ({
  width: `${Math.round((parseInt(min) / parseInt(max)) * 100)}%`
})

const FileProgress = ({ fileName, min, max }) => (
  <div className='file-progress'>
    <div className='file-progress__filename'>
      {fileName}
    </div>
    <div className='file-progress__bar-container'>
      <div className='file-progress__bar' style={progressStyles(min, max)} />
    </div>
  </div>
)

const NonFormFileProgress = ({ fileName, min, max }) => (
  <div className='non-form-file-progress'>
    <div className='non-form-file-progress__filename'>
      {fileName}
    </div>
    <div className='non-form-file-progress__bar-container'>
      <div className='non-form-file-progress__bar' style={progressStyles(min, max)} />
    </div>
  </div>
)

const FileUploadProgress = ({ filesProgress, formLayout }) => {
  const getComponent = formLayout ? FileProgress : NonFormFileProgress

  return R.map(
    getComponent
  )(filesProgress)
}

FileUploadProgress.defaultProps = {
  formLayout: true
}

export default FileUploadProgress



