import { formValueSelector, reduxForm, getFormSyncErrors } from 'redux-form'
import { connect } from 'react-redux'

import { OrganisationFileKeyPrefixes } from 'Shared/constants/Organisation'

import { fetchIndustries } from 'Shared/ducks/industries'
import { fetchPerks, fetchCompanySize } from 'Shared/ducks/resources'
import { updateOrganisation,
  uploadOrganisationImageFile,
  removeOrganisationImageFile,
  uploadOrganisationVideo
} from 'Shared/ducks/organisations'
import {
  deleteVideo,
  fetchOrganisationImages,
  deleteOrganisationImageById,
  generateS3Signature,
  uploadFileToS3
} from 'Shared/ducks/files'
import { transformToArray } from 'Shared/helpers/redux'
import { getUsersOrganisation } from 'Shared/selectors/organisations'
import { hasEmployerOrganisationCompanyPageAvailable } from 'Shared/selectors/membership'
import { getAllOrganisationImages } from 'Shared/selectors/files'
import { updateUserAttributes } from 'Chat/ducks/classes/users'

import EditCompanyDetailsForm from 'Employer/forms/Company/EditCompanyDetailsForm'
import validate from 'Employer/connectors/Forms/Company/EditCompanyDetailsFormValidator'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { scrollToFirstError } from 'Shared/helpers/scroll'

const formUtilsService = new FormUtilsService()
const mapOptionsByLanguage = formUtilsService.mapOptionsByLanguageTranslations

export const formName = 'EditCompanyDetailsForm'

let Form = reduxForm({
  form: formName,
  validate,
  shouldValidate: () => true,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true,
  keepDirtyOnReinitialize: true
})(EditCompanyDetailsForm)

const mapStateToProps = state => {
  const organisation = getUsersOrganisation(state)
  const filteredPerkEntries = organisation.perks && transformToArray(state.resources.perks.entries).filter(el => organisation.perks.includes(el.value))
  const initialPerks = filteredPerkEntries
    ? formUtilsService.mapOptionsWithCustomIdByLanguageTranslations(filteredPerkEntries, 'value', 'title')
    : []
  const socialMediaLinks = organisation.socialMediaLinks || {
    twitter: '',
    linkedin: ''
  }

  return {
    initialValues: {
      industryId: organisation.industryId,
      logoUrl: organisation.logoUrl,
      coverPhotoUrl: organisation.coverPhotoUrl,
      name: organisation.name,
      description: organisation.description,
      perks: initialPerks,
      size: organisation.size,
      socialMediaLinks,
      website: organisation.website,
      location: {
        address: organisation.address,
        city: organisation.city,
        country: organisation.country,
        locationCoords: organisation.locationCoords,
        googlePlacesId: organisation.googlePlacesId
      }
    },
    videoUrl: organisation.videoUrl,
    isVideoUploading: state.files.videoCvUploadProgress,
    coverPhotoUrl: formValueSelector(formName)(state, 'coverPhotoUrl'),
    industries: mapOptionsByLanguage(transformToArray(state.industries.entries)),
    perks: formUtilsService.mapOptionsWithCustomIdByLanguageTranslations(transformToArray(state.resources.perks.entries), 'value', 'title'),
    size: formUtilsService.mapOptionsWithCustomIdByLanguageTranslations(transformToArray(state.resources.companySize.entries), 'value', 'name'),
    canUseCompanyPageService: hasEmployerOrganisationCompanyPageAvailable(state),
    organisationId: organisation.id,
    images: getAllOrganisationImages(state, organisation.id),
    formErrors: getFormSyncErrors(formName)(state)
  }
}

const mapDispatchToProps = dispatch => ({
  updateOrganisation: async values => {
    await dispatch(updateOrganisation(values))
    dispatch(updateUserAttributes())
  },
  fetchIndustries: () => dispatch(fetchIndustries()),
  fetchPerks: () => dispatch(fetchPerks()),
  fetchCompanySize: () => dispatch(fetchCompanySize()),
  fetchImages: organisationId => dispatch(fetchOrganisationImages(organisationId)),
  uploadOrganisationLogo: async (file, filename) => {
    await dispatch(uploadOrganisationImageFile(file, filename, OrganisationFileKeyPrefixes.logoUrl))
    dispatch(updateUserAttributes())
  },
  deleteOrganisationLogo: () => dispatch(removeOrganisationImageFile(OrganisationFileKeyPrefixes.logoUrl)),
  uploadOrganisationCoverPhoto: (file, filename) => dispatch(uploadOrganisationImageFile(file, filename, OrganisationFileKeyPrefixes.coverPhotoUrl)),
  deleteOrganisationCoverPhoto: () => dispatch(removeOrganisationImageFile(OrganisationFileKeyPrefixes.coverPhotoUrl)),
  uploadVideo: (file, filename) => dispatch(uploadOrganisationVideo(file, filename)),
  deleteVideo: () => dispatch(deleteVideo()),
  generateSignature: () => dispatch(generateS3Signature(true)),
  uploadFile: (file, policy, signature) => dispatch(uploadFileToS3(file, policy, signature)),
  deleteFile: (imageId, name) => dispatch(deleteOrganisationImageById(imageId, name))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



