import React, { Component } from 'react'
import PropTypes from 'prop-types'
import EditCompanyDetailsFormConnector from 'Employer/connectors/Forms/Company/EditCompanyDetailsFormConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'

import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'

class EditCompanyDetailsView extends Component {
  static propTypes = {
    fetchIndustries: PropTypes.func.isRequired
  }

  async componentDidMount () {
    await this.props.fetchIndustries()
  }

  render () {
    return (
      <Container>
        <PageTopHeaderMobile title={this.props.t('common.editCompany')} />
        <EditCompanyDetailsFormConnector />
      </Container>
    )
  }
}

export default withTranslations(EditCompanyDetailsView)



