/* eslint-disable no-throw-literal */
import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { Form, Field } from 'redux-form'
import { isEmpty, omit, has, isNil, propOr } from 'ramda'
import {
  InputWrapper,
  InputWithIcon,
  MultiSelect,
  Editor,
  SingleImageUploader,
  Input,
  Select,
  SearchMap,
  Divider,
  VideoUploader,
  S3Uploader
} from 'Shared/components/FormElements'
import FormControls from 'Shared/components/FormControls/FormControls'
import ProfileStepSection, { UnavailableSection } from 'Shared/components/ProfileStepSection'
import withTranslations from 'Shared/hoc/withTranslations'
import { PrimaryButton } from 'Shared/components/Button'
import { lengthLimit } from 'Shared/constants/Editor'
import { MAX_PERKS } from 'Shared/constants/Organisation'
import FormContainer from 'Shared/components/FormContainer'
import PageHeading from 'Shared/components/PageHeading/PageHeading'
import UploadedImagesList from 'Shared/components/UploadedImagesList/UploadedImagesList'

import icon from 'Shared/assets/images/vectors/icon-step3-experience.svg'
import { ASPECT_RATIOS, CROP_SIZES } from 'Shared/constants/Files'
import { MAX_UPLOAD_FILE_SIZE, MAX_IMAGES_COUNT, getAcceptedAvatarFormats } from 'Shared/constants/S3'
import LocationContactConnector from 'Shared/connectors/LocationContact/LocationContactConnector'

import './EditCompanyDetailsForm.scss'
import { Helmet } from 'react-helmet'

class EditCompanyDetailsForm extends Component {
  static propTypes = {
    uploadOrganisationLogo: PropTypes.func.isRequired,
    deleteOrganisationLogo: PropTypes.func.isRequired,
    uploadOrganisationCoverPhoto: PropTypes.func.isRequired,
    deleteOrganisationCoverPhoto: PropTypes.func.isRequired,
    canUseCompanyPageService: PropTypes.bool.isRequired
  }

  async componentDidMount () {
    await this.props.fetchPerks()
    await this.props.fetchCompanySize()
    await this.props.fetchIndustries()
    await this.props.fetchImages(this.props.organisationId)
  }

  normalizeValues = values => {
    const safeValues = omit(['location'], values)

    return {
      ...safeValues,
      ...values.location,
      perks: values.perks.map(el => el.value)
    }
  }

  updateOrganisation = values => {
    return this.props.updateOrganisation(this.normalizeValues(values))
  }

  render () {
    const { t, handleSubmit, canUseCompanyPageService } = this.props
    const showUploadLogoError = isNil(this.props.initialValues.logoUrl) && has('logoUrl', propOr({}, 'formErrors', this.props)) && this.props.submitFailed

    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.employer.title.companyProfile')}</title>
          <meta property='og:title' content={t('meta.employer.title.companyProfile')} />
        </Helmet>
        <div className='edit-company-details__container parsing--hidden'>
          <Form onSubmit={handleSubmit(this.updateOrganisation)}>
            <FormContainer className={'form-container--details'}>
              <PageHeading
                heading={this.props.t('company.form.edit.title')}
                description={this.props.t('company.form.edit.description')}
                image={icon}
              />

              <Field
                component={Input}
                label={t('forms.companyName.label')}
                name='name'
                placeholder={t('forms.companyName.placeholder')}
                type='text'
              />
              <div className='columns form__wrapper--inline'>
                <div className='column col-6'>
                  <Field
                    component={Select}
                    options={this.props.industries}
                    label={t('forms.industry.label')}
                    placeholder={t('common.select')}
                    name='industryId'
                    withIcon
                  />
                </div>
                <div className='column col-6'>
                  <Field
                    component={Select}
                    options={this.props.size}
                    label={t('forms.companySize.label')}
                    placeholder={t('common.select')}
                    name='size'
                    withIcon
                  />
                </div>
              </div>
              <Field
                component={SearchMap}
                label={t('forms.location.label')}
                placeholder={t('forms.location.placeholder')}
                name='location'
              />
              <Divider />
              <ProfileStepSection
                label={t('company.form.sections.description.label')}
                text={t('company.form.sections.description.text')}
              />
              <div className='form__group'>
                <Field
                  component={Editor}
                  label={t('forms.companyDescription.label')}
                  placeholder={t('forms.companyDescription.placeholder')}
                  name='description'
                  limit={lengthLimit.companyDetailsDescription[1]}
                />
              </div>
              <div className='form__group'>
                <Field
                  component={Input}
                  label={t('forms.companyWebsiteURL.label')}
                  name='website'
                  optional={t('common.optional')}
                  placeholder={t('forms.companyWebsiteURL.placeholder')}
                  type='text'
                />
              </div>
              <div className='form__group'>
                <InputWrapper label={t('forms.companySocialProfiles.label')} optional={t('common.optional')}>
                  <Field
                    component={InputWithIcon}
                    name='socialMediaLinks.linkedin'
                    iconClassName='icon-im-linkedin'
                    placeholder={t('forms.linkedIn.placeholder')}
                    type='text'
                    showOnError
                  />
                  <Field
                    component={InputWithIcon}
                    name='socialMediaLinks.twitter'
                    iconClassName='icon-im-twitter'
                    placeholder={t('forms.twitter.placeholder')}
                    type='text'
                    showOnError
                  />
                </InputWrapper>
              </div>
              <Divider />
              <ProfileStepSection
                label={t('company.form.sections.logo.label')}
                text={t('company.form.sections.logo.text')}
              />
              <Field component={Input} name='logoUrl' type='hidden' />
              <SingleImageUploader
                multiple={false}
                uploadFile={this.props.uploadOrganisationLogo}
                deleteFile={this.props.deleteOrganisationLogo}
                imageUrl={this.props.initialValues.logoUrl}
                hasError={showUploadLogoError}
                error={propOr({}, 'logoUrl', this.props.formErrors)}
              />
              <Divider />
              <ProfileStepSection
                optional
                label={t('company.form.sections.perks.label')}
                text={t('company.form.sections.perks.text')}
              />
              <Field
                component={MultiSelect}
                options={this.props.perks}
                placeholder={t('forms.perks.placeholder')}
                name='perks'
                maxItemsCount={MAX_PERKS}
                withIcon
              />
              {
                canUseCompanyPageService
                  ? (<div>
                    <div>
                      <Divider />
                      <PageHeading
                        heading={this.props.t('company.form.extraFeatures.title')}
                        description={this.props.t('company.form.extraFeatures.description')}
                        image={icon}
                      />
                      <ProfileStepSection
                        label={t('company.form.sections.coverPhoto.label')}
                        text={t('company.form.sections.coverPhoto.text')}
                      />
                      <Field component={Input} name='coverPhotoUrl' type='hidden' />
                      <SingleImageUploader
                        multiple={false}
                        sizeConstraints={CROP_SIZES.COVER_PHOTO}
                        layout='vertical'
                        cropAspectRatio={ASPECT_RATIOS.COVER_PHOTO}
                        uploadFile={this.props.uploadOrganisationCoverPhoto}
                        deleteFile={this.props.deleteOrganisationCoverPhoto}
                        imageUrl={this.props.initialValues.coverPhotoUrl}
                      />
                    </div>
                    <Divider />
                    <ProfileStepSection
                      label={t('company.form.sections.video.label')}
                      text={t('company.form.sections.video.text')}
                    />
                    <VideoUploader
                      uploadVideo={this.props.uploadVideo}
                      deleteVideo={this.props.deleteVideo}
                      videoCvUrl={this.props.videoUrl}
                      uploading={this.props.isVideoUploading}
                    />
                    <Divider />
                    <ProfileStepSection
                      label={t('company.form.sections.images.label')}
                      text={t('company.form.sections.images.text')}
                    />
                    {this.props.images.length < MAX_IMAGES_COUNT && <S3Uploader
                      generateSignature={this.props.generateSignature}
                      uploadFile={this.props.uploadFile}
                      maxSize={MAX_UPLOAD_FILE_SIZE}
                      maxFileCount={MAX_IMAGES_COUNT}
                      formats={getAcceptedAvatarFormats()}
                      files={this.props.images}
                    />}
                    {!isEmpty(this.props.images) &&
                    <UploadedImagesList files={this.props.images} deleteFile={this.props.deleteFile} />}
                  </div>)
                  : (<div>
                    <Divider />
                    <UnavailableSection label={t('company.form.sections.unavailableMediaSections.label')}>
                      {t('company.form.sections.unavailableMediaSections.text')}&nbsp;
                      <LocationContactConnector />
                    </UnavailableSection>
                  </div>)
              }
            </FormContainer>
            <FormControls>
              <PrimaryButton type='submit' className='button--form-controls'>
                {this.props.t('common.save')}
              </PrimaryButton>
            </FormControls>
          </Form>
        </div>
      </Fragment>
    )
  }
}

export default withTranslations(EditCompanyDetailsForm)



