import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { mergeProps } from 'Shared/helpers/redux'
import EditCompanyDetailsView from 'Employer/views/EditCompanyDetailsView/EditCompanyDetailsView'
import { fetchIndustries } from 'Shared/ducks/industries'

const mapStateToProps = state => ({
  industriesLoading: state.industries.isLoading

})

const mapDispatchToProps = dispatch => ({
  fetchIndustries: () => dispatch(fetchIndustries())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(EditCompanyDetailsView))



